import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import {DataTable} from 'primereact/datatable';
import {Column} from 'primereact/column';

import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Avatar from '@material-ui/core/Avatar';
import FieldDataType from './FieldDataType';
import {udtAvatarType} from './FieldDataType';


const useStyles = makeStyles(theme => ({
    root: {
        padding: theme.spacing(1),
    },
    table: {
        minWidth: 500,
    },

    halfPointPadding: {
        padding: theme.spacing(0.5),
    },

    onePointPadding: {
        padding: theme.spacing(1),
    },

    cardTitle: {
        borderTopRightRadius: 5,
        borderTopLeftRadius: 5,
        backgroundColor: 'lightgrey',
        padding: 3
    },

}));

function RecordBased( props ) {
    const classes = useStyles();

    let udt = props.udt;
    let udtDetailsComp = props.udtDetailsComp;
    let fqn = udt.Name.FullyQualifiedName
    var n = fqn.lastIndexOf(".");
    var namespace = ""

    if ( n > 0 )
        namespace = fqn.substr(0,n);

    function keyDecl(key) {
        if ( key == null )
            return null

        else if ( key.KeyRef ) {
            return (
            <div className={classes.onePointPadding}>
                <b>Key</b>
                <span className={classes.onePointPadding}>
                    <FieldDataType parentNamespace={namespace} dtype={key.KeyRef} withAvatar={false} udtDetailsComp={udtDetailsComp} />
                </span>
            </div>
           )
        }
        else if ( key.KeyFields ) {
//                                <Column field="default" header="Default" style={{width: '10em'}} />

            return (
            <div>
                <Paper>
                    <div className={classes.cardTitle}>
                        <Typography variant="h6" component="h3" >
                            Key Fields
                        </Typography>
                    </div>

                    <DataTable value={
                        key.KeyFields.map( (f,index) => {
                            return {
                                    key: f.index,
                                    name: f.Name,
                                    type: f.DataType,
                                    doc: f.Doc,
                                    default: f.DefaultValue
                            }
                        })
                    }>
                        <Column field="name" header="Name" style={{width: '15em'}}/>
                        <Column field="type" header="Type" style={{width: '20em'}} body={dataTypeTemplate} />
                        <Column field="doc" header="Comments"  />
                    </DataTable>     
                </Paper>
            </div> )          
        }
        else 
            return null
    }

    function includesDecl(incs) {
        if ( incs == null || incs.length == 0 )
            return null
        else
            return (
                <span className={classes.onePointPadding}>
                    <b>Includes:</b>
                    { incs.map( (e,index) => {
                        return ( <span key={index} className={classes.halfPointPadding}>
                            <FieldDataType parentNamespace={namespace} dtype={e} udtDetailsComp={udtDetailsComp} />
                        </span> )
                    } ) }
                </span>
            );
    }

    function showDependants( deps ) {

        if ( deps == null || deps.length == 0 )
            return null
        else

            return (
                <div className={classes.onePointPadding}>
                    <ExpansionPanel>
                        <ExpansionPanelSummary
                            expandIcon={<ExpandMoreIcon />}
                            aria-controls="usage-content"
                            id="usages-header"
                        >
                            <Typography className={classes.heading}>Usages</Typography>
                        </ExpansionPanelSummary>
                        <ExpansionPanelDetails>
                            {
                                <Box display="flex" flexWrap="wrap">
                                {
                                    deps.map( (d,index) => {
                                        if ( d.IsSynthetic === true )
                                            return (<span key={index}/>)
                                        else
                                            return ( <div key={index} >

                                                    <div style={{padding:'5px 5px 5px 5px' }}>
                                                        <FieldDataType parentNamespace={namespace} dtype={d} withAvatar={true} udtDetailsComp={udtDetailsComp} />
                                                    </div>
                                            </div> )
                                    } )
                                }
                                </Box>
                            }
                        </ExpansionPanelDetails>
                    </ExpansionPanel>
                </div>
        )
    }

    function makeTableData() {
        return udt.LocalFieldNames.map( (fieldName,index) => {
            let f = udt.AllFields[fieldName]
            return {
                    key: index,
                    name: f.Name,
                    type: f.DataType,
                    doc: f.Doc,
                    default: f.DefaultValue
            }
        });
    } 

    function dataTypeTemplate(rowData, column) {
        return <span><FieldDataType parentNamespace={namespace} dtype={rowData['type'] } withAvatar={true} udtDetailsComp={udtDetailsComp} /></span>;
    }

    let ut = udt.Name.UdtType;

//            <Column field="default" header="Default" style={{width: '10em'}} />

    let colDef = ( 
        <DataTable value={makeTableData()}>
            <Column field="name" header="Name" style={{width: '15em'}}/>
            <Column field="type" header="Type" style={{width: '20em'}} body={dataTypeTemplate} />
            <Column field="doc" header="Field Comments" />
        </DataTable>        
    )

    if ( ut === 'enumType') {
        colDef = ( 
            <DataTable value={makeTableData()}>
            <Column field="name" header="Name" style={{width: '15em'}} />
            <Column field="doc" header="Comments" />
        </DataTable>
        )
    }

    return (
        <div className={classes.root}>
            <Paper className={classes.root}  >
                <div className={classes.cardTitle}>

                    <Grid container direction="row" alignItems="center">
                        <Grid item>
                            <div className={classes.halfPointPadding}><Avatar>{udtAvatarType(udt.Name.UdtType  )}</Avatar></div>
                        </Grid>
                        <Grid item>
                            <Typography variant="h5" component="h3">
                                {udt.Name.FullyQualifiedName}
                            </Typography>
                        </Grid>
                    </Grid>
                </div>

                <div className={classes.onePointPadding}>
                    <Typography component="p">
                        {udt.Doc}
                    </Typography>

                    <div style={{ width: '100%' }}>
                        <Box display="flex" flexWrap="wrap">
                            <div className={classes.onePointPadding}>
                            </div>
                        </Box>

                        { includesDecl(udt.Includes) }
                        { keyDecl(udt.Key) }
                    </div>
                </div>


                <div className={classes.onePointPadding}>
                    <Paper>
                        <div className={classes.cardTitle}>
                            <Typography variant="h6" component="h3" >
                                Fields
                            </Typography>
                        </div>

                        { colDef }
                    </Paper>
                </div>

                { showDependants(udt.ImmediateDependants) }

            </Paper>
        </div>
    );
}

export default RecordBased;